import React, { useState } from "react";
import "./BoxComponent.css";
import coin from "../images/coin.png";
import decline from "../images/decline.png";
import { Spring, animated, useSpring } from "react-spring";
import getRandomNumber from "./RandomNum";

function BoxComponent() {
  const [rNum, setrNum] = useState(1);

  const styles = useSpring({
    loop: true,
    to: [
      { opacity: 1, color: "#ffaaee" },
      { opacity: 0, color: "rgb(14,26,19)" },
    ],
    from: { opacity: 0, color: "red" },
  });

  let imgArr = [coin, decline];

  function handleClick() {
    setrNum(getRandomNumber);
  }

  return (
    <div>
      <div className="container">
        <animated.div style={styles}>Try your luck</animated.div>

        <img
          className="imgs"
          src={rNum === 1 ? coin : decline}
          alt="random picked "
        />

        <h1>{rNum == 0 ? "Lost" : "Won"}</h1>
        <button onClick={handleClick}>
          Click <span className="slickText">Me!</span>
        </button>
      </div>
    </div>
  );
}

export default BoxComponent;
